const app = getApp().globalData;
let { navBar } = app;
export default {
	data() {
		return {
			date:'请选择日期',
			bitmap: true,
			navBar: navBar
		};
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			});
		},
		detailPage() {
			uni.navigateTo({
				url: 'billdetail'
			});
		},
		openCalendar(){
			this.$refs.calendar.open()
		},
		confirm(e){
			const length=e.range.data.length-1;
			this.date=`${e.range.data[0]},${e.range.data[length]}`
		}
	},
	onShow() {}
};
